.header {
  background: #FFFFFF;
  position: relative;
  overflow: hidden;
  .container {
    padding-right: 0;
  }
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__logo {
    width: 226px;
    img {
      width: 100%;
    }
  }
  &__img {
    width: 590px;
    margin-right: -225px;
    img {
      width: 100%;
      height: 100%;
      display: block;
    }
  }
}

@media screen and (max-width: 1100px) {
  .header {
    &__img {
      margin-right: -160px;
    }
  }
}

@media screen and (max-width: 992px) {
  .header {
    &__logo {
      width: 180px;
    }
    &__img {
      width: 400px;
      margin-right: -100px;
    }
  }
}

@media screen and (max-width: 900px) {
  .header {
    &__img {
      margin-right: -100px;
    }
  }
}

@media screen and (max-width: 768px) {
  .header {
    &__img {
      width: 350px;
      margin-right: 0;
    }
  }
}

@media screen and (max-width: 576px) {
  .header {
    &__logo {
      width: 120px;
      padding: 5px 0;
    }
    &__img {
      width: 300px;
      margin-right: -50px;
    }
  }
}