.email {
  padding: 45px 0 120px;
  &__inner {
    max-width: 730px;
    margin: 0 auto;
  }
  &__title {
    font-family: 'HelveticaNeue';
    font-size: 26px;
    line-height: 120%;
    font-weight: 700;
    letter-spacing: 0.01em;
    color: #0E0E0E;
    text-align: center;
  }
  &__desc {
    background: #FFFFFF;
    border-radius: 4px;
    padding: 30px;
    margin-top: 40px;
  }
  &__subtitle {
    font-size: 23px;
    line-height: 120%;
    letter-spacing: 0.01em;
    color: #0E0E0E;
    text-align: center;
  }
  &__text {
    margin-top: 30px;
    p {
      font-size: 19px;
      line-height: 150%;
      letter-spacing: 0.01em;
      color: #0E0E0E;
      text-align: justify;
      text-indent: 35px;
      &:not(:first-child) {
        margin-top: 20px;
      }
    }
  }
  &__link {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 221px;
    height: 48px;
    background: #1AB373;
    border-radius: 6px;
    margin-top: 30px;
    font-size: 16px;
    line-height: 120%;
    font-weight: 700;
    letter-spacing: 0.01em;
    text-transform: uppercase;
    color: #FFFFFF;
    &:hover {
      background: #0aa166;
    }
  }
  &__list {
    margin-top: 30px;
    li {
      font-size: 19px;
      line-height: 150%;
      text-align: justify;
      letter-spacing: 0.01em;
      color: #0E0E0E;
      padding-left: 17px;
      position: relative;
      &:not(:first-child) {
        margin-top: 10px;
      }
      &:after {
        content: '';
        position: absolute;
        top: 7px;
        left: 0;
        width: 7px;
        height: 7px;
        background: #1AB373;
        border-radius: 50%;
      }
    }
  }
  &__info {
    background: #DBE1E0;
    border-radius: 4px;
    padding: 30px 68px;
    margin-top: 40px;
    text-align: center;
    font-size: 17px;
    line-height: 140%;
    letter-spacing: 0.01em;
    color: #776E6E;
    a {
      color: #0B7046;
      text-decoration: underline;
    }
  }
}

@media screen and (max-width: 768px) {
  .email {
    padding: 30px 0 60px;
    &__title {
      font-size: 24px;
    }
    &__desc {
      margin-top: 30px;
    }
    &__subtitle {
      font-size: 20px;
    }
    &__text {
      margin-top: 20px;
      p {
        font-size: 16px;
        &:not(:first-child) {
          margin-top: 15px;
        }
      }
    }
    &__link {
      width: 200px;
      height: 40px;
      font-size: 14px;
      margin-top: 20px;
    }
    &__list {
      margin-top: 20px;
      li {
        font-size: 16px;
        &:after {
          top: 5px;
        }
      }
    }
    &__info {
      font-size: 16px;
      margin-top: 30px;
      padding: 30px;
    }
  }
}

@media screen and (max-width: 576px) {
  .email {
    &__desc {
      padding: 30px 15px;
    }
    &__info {
      padding: 30px 15px;
    }
  }
}